import React from "react";
import { css } from "styled-components";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";

const checkboxCheckStyle = css`
  background-color: #9060EB;
  border-color: #9060EB;
`;

const theme = deepMerge(grommet, {
	accordion: {
		border: undefined,
		icons: {
			collapse: () => (
				<i
					className="zwicon-chevron-up"
					style={{
						color: "#9060EB",
						fontSize: "32px",
					}}
				/>
			),
			expand: () => (
				<i
					className="zwicon-chevron-down"
					style={{
						color: "#9060EB",
						fontSize: "32px",
					}}
				/>
			),
		},
	},
	// button: {
	// 	color: {
	// 		primary: "#000",
	// 		secondary: "#fff"
	// 	}
	// },
	formField: {
		border: {
			position: "none"
		}

		// border-bottom: 1.5px solid #CCCCCC;
		// font-size: 14px;
		// &:focus {
		// 	border-bottom-color: #9060EB !important;
		// }
		// label: {
		// 	margin: {
		// 		vertical: "small",
		// 	},
		// },
	},
	global: {
		colors: {
			brand: "#009746",
			error: "#FF4040",
			nav: "rgba(255, 255, 255, 0.7)",
			secondary: "#9060EB",
			success: "#00C781",
			text: { secondary: "#fff" },
			transparent: "rgba(255, 255, 189, 0)",
			warning: "#FFAA15",
		},
		control: {
			border: {
				radius: 0,
			},
		},
		focus: {
			border: {
				color: "#9060EB",
			},
		},
		font: {
			family: "HelveticaNeue",
		},
	},
	table: {
		body: {
			align: "center",
			border: undefined,
			pad: { vertical: "small" },
			row: {
				color: "red",
			}
		},
		extend: {
			fontSize: "14px !important",
		},
		footer: {
			align: "start",
			border: undefined,
			pad: { horizontal: "large", vertical: "small" },
			verticalAlign: "bottom",
		},
		header: {
			align: "center",
			border: undefined,
			fill: "horizontal",
			pad: { horizontal: "large", vertical: "xsmall" },
			verticalAlign: "bottom",
		},
	},
	text: {
		extend: {
			textDecoration: "none !important",
		},
		medium: {
			size: "14px",
		},
	},
	textInput: {
		extend: () => `
			border: none;
			border-bottom: 1.5px solid #CCCCCC !important;
			font-size: 14px;
			&:focus {
				border-bottom-color: #9060EB !important;
			}
		`,
	},
	textArea: {
		extend: () => `
			border: none;
			border-bottom: 1.5px solid #CCCCCC !important;
			font-size: 14px;
			&:focus {
				border-bottom-color: #9060EB !important;
			}
		`,
	},
	maskedInput: {
		extend: () => `
			border: none;
			border-bottom: 1.5px solid #CCCCCC !important;
			font-size: 14px;
			&:focus {
				border-bottom-color: #9060EB !important;
			}
		`,
	},
	select: {
		icons: {
			color: "#9060EB",
		},
		control: {
			extend: {
				border: "none !important",
			},
		}
	},
	checkBox: {
		border: {
			color: {
				light: "#757575"
			}
		},
		color: {
			light: "#ffffff"
		},
		hover: {
			border: {
			  color: undefined
			}
		},
		toggle: {
			background: { light: "#fff" },
			color: {
				light: "#fff"
			},
			size: "36px",
			knob: {
				extend: `
				top: -4px;
				box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.12), 0px 2px 2px 0px rgba(0,0,0,0.24);
				`
			},
			extend: ({ checked }: { checked: any }) => `
				height: 14px;
				${checked && checkboxCheckStyle}
			`,
		},
		gap: "xsmall",
		size: "18px"
	}
});

export default theme;
